const n = (t, o = "xdd-theme") => {
  console.log("className: ", o);
  const s = document.getElementsByClassName(o);
  for (let e = 0; e < s.length; e++) {
    const c = s[e];
    Object.keys(t).forEach((l) => {
      c.style.setProperty(`--${l}`, t[l]);
    });
  }
};
export {
  n as setThemeStyle
};
